body{
    
     margin: 0;
}
.Homepage{
    background: url(./transport.jpg);
    background-size: cover;
    margin: 0;
    height: 90vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
   /* justify-content: center; */
}
.board{
    display: flex; 
    flex-wrap: wrap;
    
}
.board1 {
    background-color: rgba(250, 7, 7, 0.7);
    color: white;
    width: 350px;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
    border-radius: 15px;
   
    
}
.board2 {
    background-color: rgb(241, 240, 248);
    align-self: stretch;
    width: 350px;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
    border-radius: 15px;
    
}
.boardButton{
    background-color: transparent;
    color: white;
    height: 40px;
    width: 150px;
    border: 1px solid white;
   
}
.navLink{
    display: inline-block;
    font-size: 18px;
    margin: 5px 15px;
    
}
.navUl{
    display: flex;
    justify-content: flex-end;
    background-color: white;
    width: 90vw;
    padding: 5px;
    margin: 0;
    
}
.navLink:hover{
    
    text-decoration: underline;
    color: blue;
    font-weight: bold;
    cursor: pointer;
}
.loginForm{
    /* margin: auto; */
    display: flex;
    justify-self: stretch;
    flex-direction: column;
    width: 500px;
    height: 200px;
    background-color: #fff;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}
.loginForm >input{
    height: 30px;
    width: 300px;
    margin-bottom: 10px;
    border-radius: 5px;
    outline: none;
    border: 1px solid gray;
}
.loginForm >input:hover{
    background-color: beige;
    
}
.loginButton{
    color: black;
    background-color: transparent;
    border: 1px solid black;
    padding: 5px;
    width: 70px;
    margin-right: 95px;
}
.loginSpan{
    color: black;
}
.linkToSignup{
    color: tomato;
}
.linkToSignup:hover{
    font-weight: bold;
    text-decoration: underline;
    cursor: pointer;
}
.loginLabel{
    color: black;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    
}
.signupForm{
    width: 500px;
    height: 500px;
    background-color: #FFF
}
.signupLabel{
    color: black;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 20px;
}
.loginPage{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fafafa;
    height: 90vh;
   
}
.Fabar{
    color: blue;
}
.bar{
    display: none;
    background-color: white;
    width: 90vw;
    height: 50px;
}
.sideBar{
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100%;
    background-color: #0d0d0d;
    display: grid;
    align-items: center;
    top: 0;
    left: 0;
    transition: 0.3s ease-in-out;
    
}
.close{
    color: #fff;
}
.icon{
    position: absolute;
    top: 1.2rem;
    right: 1.5rem;
    background-color: transparent;
    font-size: 2rem;
    cursor: pointer;
    outline: none;
}
.sidebarUl{
    display: grid;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-color: white; */
    width: 90vw;
    padding: 5px;
    margin: 0; 
}
.sidebarLink{
   list-style: none;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    width: 400px;
    padding: 15px 0;
    transition: 0.2s ease-in-out;   
    opacity: 100%;
    top: 0;
}
.false{
   
        /* list-style: none;
         font-size: 30px;
         font-weight: bold;
         text-align: center;
         width: 400px;
         padding: 15px 0;
         transition: 0.2s ease-in-out;    */
         opacity: 0;
         top: -100;
         display: none;
     
}
.dispNone{
    display: none;
}
.sidebarLink:hover{
   color: chartreuse;
   transition: 0.2s ease-in-out;  
}

footer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    background-color: black;
    color: white;
    align-self: flex-end;
    width: 100vw;
}
.socialMedia{
    grid-column: span 2;
    display: flex;
    flex-direction: row;
}
.socialMedia > h4{
    margin-left: 20px;
}
@media screen and (max-width: 768px) {
    .navUl{
            display: none; 
            
    }
.board1, .board2{
    opacity: 0.7;
    width: 100vw;
}

    .Fabar{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-90%, 35%); 
        font-size: 1.8rem;
        cursor: pointer;
        
    }
    .bar{
        display: block;
    }
  }
  

